'use client';

import { useEffect, useState } from 'react';
import { useParams } from 'next/navigation';
import Link from 'next/link';
import { Prato } from '@/types/types';

interface Prato {
  id: number;
  categoria: string;
  nome: string;
  descricao: string;
  imagem: string;
}

export default function ListaEntradas() {
  const params = useParams<{ categoria: string }>();
  const [pratos, setPratos] = useState<Prato[]>([]);

  useEffect(() => {
    fetch('/data/pratos.json')
      .then(res => res.json())
      .then((data: Prato[]) => {
        const filtrados = data.filter(prato => 
          prato.categoria.toLowerCase() === params.categoria
        );
        setPratos(filtrados);
      });
  }, [params.categoria]);

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold text-emerald-700 mb-8">
        {params.categoria.charAt(0).toUpperCase() + params.categoria.slice(1)}
      </h1>
      
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
        {pratos.map(prato => (
          <Link 
            key={prato.id} 
            href={`/pratos/${params.categoria}/${prato.id}`}
            className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow"
          >
            <img 
              src={prato.imagem} 
              alt={prato.nome} 
              className="w-full h-48 object-cover"
            />
            <div className="p-4">
              <h2 className="text-xl font-semibold text-gray-800">{prato.nome}</h2>
              <p className="text-gray-600 mt-2">{prato.descricao}</p>
            </div>
          </Link>
        ))}
      </div>
    </div>
  );
}